'use client';

import { useState, useEffect } from 'react';
import PrivateRoute from "@/app/components/PrivateRoute";
import Footer from "@/app/components/Footer";
import { useAuth } from '@/contexts/AuthContext';
import { collection, query, where, getDocs } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { useRouter } from 'next/navigation';

export default function ConsultaPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [mascotas, setMascotas] = useState([]);
  const [loadingMascotas, setLoadingMascotas] = useState(true);
  const [selectedMascota, setSelectedMascota] = useState('');
  const [fecha, setFecha] = useState('');
  const [hora, setHora] = useState('');
  const [motivo, setMotivo] = useState('');

  useEffect(() => {
    if (!user) return;

    const fetchMascotas = async () => {
      try {
        const q = query(collection(db, 'mascotas'), where('dueño_uid', '==', user.uid));
        const querySnapshot = await getDocs(q);
        const listaMascotas = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        setMascotas(listaMascotas);
      } catch (error) {
        console.error("Error al cargar las mascotas: ", error);
      } finally {
        setLoadingMascotas(false);
      }
    };

    fetchMascotas();
  }, [user]);

  const handleSubmit = (e) => {
    e.preventDefault();
    // Lógica para guardar el turno en Firestore
    console.log({ selectedMascota, fecha, hora, motivo });
    alert('Turno de consulta solicitado (simulación)');
  };

  return (
    <PrivateRoute>
      <main className="container mx-auto px-4 py-8">
        <div className="flex justify-between items-center mb-8">
            <h1 className="text-3xl font-bold">Turno de Consulta</h1>
            <button onClick={() => router.push('/')} className="bg-gray-500 hover:bg-gray-700 text-white font-bold py-2 px-4 rounded-lg">
              Volver
            </button>
        </div>
        <form onSubmit={handleSubmit} className="max-w-lg mx-auto bg-white p-8 rounded-lg shadow-md">
          
          <div className="mb-4">
            <label htmlFor="mascota" className="block text-gray-700 font-bold mb-2">Mascota</label>
            <select id="mascota" value={selectedMascota} onChange={(e) => setSelectedMascota(e.target.value)} className="w-full px-3 py-2 border rounded-lg" required disabled={loadingMascotas}>
              <option value="">{loadingMascotas ? 'Cargando...' : 'Selecciona una mascota'}</option>
              {mascotas.map(m => <option key={m.id} value={m.id}>{m.nombre}</option>)}
            </select>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
            <div>
              <label htmlFor="fecha" className="block text-gray-700 font-bold mb-2">Fecha</label>
              <input type="date" id="fecha" value={fecha} onChange={(e) => setFecha(e.target.value)} className="w-full px-3 py-2 border rounded-lg" required />
            </div>
            <div>
              <label htmlFor="hora" className="block text-gray-700 font-bold mb-2">Hora</label>
              <input type="time" id="hora" value={hora} onChange={(e) => setHora(e.target.value)} className="w-full px-3 py-2 border rounded-lg" required />
            </div>
          </div>

          <div className="mb-6">
            <label htmlFor="motivo" className="block text-gray-700 font-bold mb-2">Motivo de la Consulta</label>
            <textarea id="motivo" value={motivo} onChange={(e) => setMotivo(e.target.value)} rows="4" className="w-full px-3 py-2 border rounded-lg" placeholder="Describe los síntomas o el motivo de la visita." required></textarea>
          </div>

          <button type="submit" className="w-full bg-violet-500 hover:bg-violet-700 text-white font-bold py-3 px-4 rounded-lg">
            Solicitar Turno
          </button>
        </form>
      </main>
      <Footer />
    </PrivateRoute>
  );
}
